.header {
    background-color: #2caee2;
    position: fixed;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 50px;
    border-bottom: 1px solid black;
    z-index: 100;
}

.container {
    width: 1100px;
    height: 50px;
    display: flex;
    justify-content: space-between;
}



.img {
    width: 130px;
    height: 130px;
    z-index: 3;
    position: fixed;
    margin-left: -5px;
}

.info {
    display: flex;
    align-items: center;
}

.info span{
    margin: 0 20px;
}

.containerNav {
    position: fixed;
}

.NavLink {
    position: fixed;
    display: flex;
    width: 650px;
    top: 65px;
    flex-direction: row;
    justify-content: flex-end;
    z-index: 51;
}


.NavLink::before {
    content: "";
    display: block;
    background-color: white;
    height: 48px;
    top: 51px;
    width: 100%;
    left: 0px;
    position: fixed;
    z-index: 1;
 
}

.NavLink::after {
    content: "";
    display: block;
    background-color: black;
    height: 1px;
    top: 100px;
    width: 100%;
    left: 0px;
    position: fixed;
}

.NavLink a {
    display: flex;
    margin: 0 20px;
    text-decoration: none;
    color: black;   
    z-index: 51;
}

.NavLink a:hover {
    color: #2caee2;
    transition: all .2s ease;

}
