.objects {
    display: flex;
    justify-content: center;
    margin: 0 auto 0;
    width: 1100px;
}

.objects {
    margin-top: 30px;
    font-weight: 700;
    text-transform: uppercase;
}

.cards_container {
    display: flex;
    width: 1100px;
    position: relative;
    top: 70px;
    justify-content: space-between;
}


.cards_container_second {
    display: flex;
    width: 1100px;
    position: relative;
    top: 70px;
    margin: 0 auto 0;
    justify-content: space-between;
}

.cards_container_second p {
    margin-top: 0px;
}


.bannerText {
    position: absolute;
    right: 0px;
    top: 50px;
}


.bannerText span {
    font-size: 40px;
    color: #0090ff;
}

.bannerText p {
    font-size: 35px;
    position: relative;
    left: 70px;
    top: -40px;
    color: #0e10b3;
}


.containerObject span {
    max-width: 215px;
    width: 100%;
    height: 100px;
    display: block;
}

.cards_container ul{
    width: 262.5px;
}

.ids {
    text-decoration: none;
}

.ids p {
    margin-top: -10px;
    font-weight: 600;
    color: black;
    padding: 0 5px;
    font-size: 18px;
}

.ids img {
    opacity: 0.8;
    border: 1px solid black;
}

.opacity{
    display: none;
    color: white;
}

.ids:hover .opacity{
    display: block;
    color: white;
    position: absolute;
    top: 40px;
    margin: 0 auto 0;
    display: flex;
    justify-content: center;
    width: 220px;
    z-index: 50;
  }


.ids:after {
    background-color: rgb(201, 198, 198);
    content: "";
    width: 230px;
    height: 189px;
    display: block;
    position: absolute;
    top: 17px;
    margin-left: 1px;
    opacity: 0;
    border-radius: 5px;
    
}

.ids:hover:after {
   opacity: 1;
   transition: all .5s ease ;
}


.description {
  
    padding: 0 5px;
    opacity: 0.7;
    color: black;
    font-size: 15px;
}


.more{
    text-decoration: none;
    position: relative;
    top: -105px;
    left: 60px;
    color: white;
    border: 1px solid white;
    padding: 5px 10px;
    border-radius: 5px;
    z-index: 10;
}


