body {
  height: 100vh;
}

#root {
  height: 100vh;
}

.App {
  text-align: center;
}

html {
  scroll-behavior: smooth;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




.container-id {
  display: flex;
  width: 1100px;
  justify-content: space-between;
  margin: 0 auto 0;
  padding-bottom: 50px;
}

.container-left {
  width: 388px;
  padding: 10px;
  margin-top:25px;
  margin-left: 40px;
}



.container-right {
  display: flex;
  flex-direction: column;
  width: 580px;
  padding: 10px;
  margin-top: 20px;
  margin-right: 40px;
  line-height: 20px;
}

.container-right span{
  margin: 20px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container-right span a {
  margin: 0px 10px;
  color: red;

}

.container-right p {
  margin: 20px 0px;
}

.container-right img {
  width: 580px;
  height: 350px;
  object-fit: cover;

}

.sale-product-cart__map iframe {
  border: 2px solid black;
  border-radius: 15px;
}

.right-text {
  line-height: 20px;
}

.numbers {
  display: flex;
  width: 560px;
  flex-direction: column;
  line-height: 30px;
  position: relative;
  align-items: flex-end;
}

.numbers a{
 text-decoration: none;
}

h3 {
  margin: 0;
}

.footer {
  position: absolute;
  left: 0;
  bottom: -30px;
  width: 100%;
  height: 70px;
  background-color: #0090ff;
}

.wrapper {
  position: relative;
  min-height: 140%;
}

.footer div {
  display: flex;
  margin: 0 auto 0;
  justify-content: center;
}


.action-container {
  margin: 0 auto 0;
  display: flex;
  flex-direction: column;
  width: 1090px;
  position: relative;
  top: 50px;
}


.action-suptitle {
  line-height: 20px;
  font-size: 16px;
}

.action-co p{
  margin-top: 50px;
  color: blue;
}

.action-co li {
 line-height: 30px;
}

.action-co li::marker {
  color: blue;
}

.action-rent {
  display: flex;
  font-size: 14px;
  margin-top: 50px;
  font-weight: 700;
  flex-direction: column;
}

.action-rent span {
  display: flex;
  font-size: 16px;
  align-items: center;
}

.action-rent p{
  font-size: 16px;
  position: relative;
  left: 10px;
}

.action-address {
  margin-top: 50px;
  font-size: 16px;
  line-height: 20px;
}

.action-address span {
  top: 30px;
  position: relative;
}

.action-suptitle i {
  color: orangered;
  font-size: 18px;
}

.text {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 0;
}

pre {
    margin: 0;
    padding: 0;
    line-height: 20px;
    font-family: none;
}

.discount {
  margin: 0px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 3px solid red;
  border-radius: 10px; 
  padding: 10px;
  line-height: 40px;
  width: 300px;
  
}

.discount span {
  color: red;
}


.lastprice {

  display: flex;
  align-items: center;
}

.newprice {
  display: flex;
  align-items: center;
  font-size: 22px;
  justify-content: center;
  position: relative;
  top: -15px;
  margin: 10px 0px;
}

.date-discount {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 700;
}

.lastprice p {
  text-decoration:line-through;
  color: red;
}


.lastprice span {
  margin: 0 20px;
  color: red;
  font-size: 20px;
}